.sidePanel {
  position: fixed;
  top: 5vh;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  z-index: 10;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;

  max-height: 90vh;
  width: 27.5rem;
  padding: 0;
  margin-left: 2.5rem;

  -webkit-transition: -webkit-transform 0.4s ease-in;

  transition: -webkit-transform 0.4s ease-in;

  -o-transition: transform 0.4s ease-in;

  transition: transform 0.4s ease-in;

  transition: transform 0.4s ease-in, -webkit-transform 0.4s ease-in;
}

.sidePanel--closed {
  -webkit-transform: translateX(-22.5rem);
      -ms-transform: translateX(-22.5rem);
          transform: translateX(-22.5rem);
  -webkit-transition: -webkit-transform 0.4s ease-out;
  transition: -webkit-transform 0.4s ease-out;
  -o-transition: transform 0.4s ease-out;
  transition: transform 0.4s ease-out;
  transition: transform 0.4s ease-out, -webkit-transform 0.4s ease-out;
}

.sidePanel > * {
  opacity: 1;
  -webkit-transition: opacity 0.4s ease-in;
  -o-transition: opacity 0.4s ease-in;
  transition: opacity 0.4s ease-in;
}

.sidePanel--closed .sidePanel__langControls,
.sidePanel--closed .sidePanel__simpleList {
  opacity: 0;
  -webkit-transition: opacity 0.4s ease-out;
  -o-transition: opacity 0.4s ease-out;
  transition: opacity 0.4s ease-out;
}

.sidePanel__controls {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 2.5rem;
  padding-bottom: 0;
}

.sidePanel__controlsRow {
  display: -ms-flexbox;
  display: flex;
  padding: 0.5rem 0;
  border-bottom: 1px solid var(--thl-gray-1);
}

.sidePanel__langControls {
  -ms-flex: 5 1;
      flex: 5 1;
  margin: auto 0;
  color: var(--thl-ui-text);
}

.sidePanel__langSelector {
  margin-right: 0.5rem;
}

.sidePanel__toggleControl {
  -ms-flex: 1 1;
      flex: 1 1;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);

  border: none;
  background: none;

  color: black;
  opacity: 0.9;
  cursor: pointer;
  -webkit-transition: 0.1s linear;
  -o-transition: 0.1s linear;
  transition: 0.1s linear;
}

.sidePanel__toggleControl > img {
  height: 4rem;
}

.sidePanel__toggleControl--rotate {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  -webkit-transition: 0.1s linear;
  -o-transition: 0.1s linear;
  transition: 0.1s linear;
}

.sidePanel__toggleControl:hover {
  opacity: 1;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

.sidePanel__toggleControl--rotate:hover {
  -webkit-transform: rotateY(180deg) scale(1.1);
          transform: rotateY(180deg) scale(1.1);
}

.sidePanel__simpleList {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 2.5rem;
  padding-top: 0;
  margin: 0;
}



